<template>
  <v-container>
    <v-row class="text-center">
      <v-col cols="12">
        <v-img
          :src="require('../assets/logo.svg')"
          class="my-3"
          contain
          height="200"
        />
      </v-col>
      {{mySire.length}}
      <v-col cols="2">
      </v-col>
      <v-col cols="8">
        <v-select
          :options="mySire"
          v-model="Sire_data"
          solo
        >
        </v-select>
      </v-col>
      <v-col cols="2">
      </v-col>


      <v-col cols="6">
        <v-text-field 
         v-model="msg"
         label="入力してください"
         />
      </v-col>
      <v-col cols="6">
         <v-btn v-on:click="submit">Submit</v-btn>
         <v-btn v-on:click="erase">Erase cache</v-btn>
      </v-col>
      <v-col cols="12">
        以下に登録したメッセージが記載されます<br>
        <v-data-table
         :headers="msg_table_header"
         :items="msg_table"
         >
        </v-data-table>
      </v-col>

    </v-row>
  </v-container>
</template>

<script>
  import Dexie from 'dexie';
  export default {
    name: 'HelloWorld',

    data: () => ({
      msg:"",
      dexie_db: null,
      Sire_data: "",
      mySire:[],
      Dam_data: "",
      myDam:[],



      msg_table_header:[
        {
          text: 'Submitted Message',
          align: 'start',
          value: 'message',
        },
        {
          text: "Timestamp",
          value: "timestamp",
        }
      ],
      msg_table: [
        {
          message: "Default",
          timestamp: "",
        },
      ]
    }),
    methods:{
      gettime(){
        var now = new Date();
        var timestamp = 
          now.getFullYear() + '/' + (now.getMonth()+1) + '/' + now.getDate()
          + '  ' + ('0'+now.getHours()).slice(-2) + ':' + ('0' + now.getMinutes()).slice(-2); 
        return timestamp;
      },

      timestampinitializer(){
        this.msg_table[0].timestamp = this.gettime();
      },

      submit(){
        var timestamp_ = this.gettime()
        this.msg_table.push({
          message:this.msg,
          timestamp:timestamp_,
        }),
        this.dexie_db.MsgDB
          .put(this.msg_table[this.msg_table.length-1]);
      },

      erase(){
        this.dexie_db.MsgDB.clear();
        this.initializer();
      },

      dbinitializer(){
        this.dexie_db = new Dexie('DB-PD-MAKER');
        this.dexie_db.version(1).stores({
          MsgDB: 'message,timestamp'
        });
        this.dexie_db.open();
        this.dexie_db.MsgDB
          .put(this.msg_table[0]);
      },
      
      tableinitializer(){
        this.dexie_db.MsgDB.toArray()
          .then(list =>{
              this.msg_table=list
            });
      },

      initializer(){
        this.timestampinitializer();
        this.dbinitializer();
        this.tableinitializer();
        fetch('./assets/default_pedigree.json')
          .then(response => {
              return response.json();
          })
          .then(json => {
              this.mySire = json.colt_data;
              this.myDam = json.filly_data;
          })
      }
    },    
    mounted(){
        this.initializer();
    },

  }
</script>
